.daily-rectangle {
  background: #ffffff;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  border-radius: 3px;
  width: 100%;
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 30px;
}

.daily-rectangle:before {
  content: "";
  display: block;
  position: absolute;
  left: -25px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border: 12px solid transparent;
  border-right-color: #e5e5e5;
}

.daily-rectangle:after {
  content: "";
  display: block;
  position: absolute;
  left: -22px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border: 11px solid transparent;
  border-right-color: white;
}

