.RFS-StepperContainer.stepclass {
  position: relative;
  padding: 0px 0px;
  margin: 30px auto;
  max-width: 600px;
}

.stepclass:after {
  content: "";
  background: #e5e5e5;
  border-radius: 30px;
  position: absolute;
  width: 100%;
  height: 5px;
  left: 0px;
  top: 15px;
  z-index: -1;
}

.stepclass .RFS-ConnectorContainer {
  display: none;
}

.stepclass .stepclassName {
  display: none;
}

.stepclass .stepclassName.active,
.stepclass .stepclassName.completed {
  display: block;
}

.stepclass .RFS-StepMain {
  position: relative;
}

.stepclass .RFS-StepContainer {
  padding-left: 0px;
  padding-right: 0px;
}

.stepclass .stepclassName.completed:after {
  content: "";
  background: #49b8ad;
  width: 100%;
  height: 5px;
  position: absolute;
  left: 0px;
  top: 15px;
  z-index: 1;
}

.stepclass .stepclassName.active:after {
  content: "";
  background: #49b8ad;
  width: 43%;
  height: 5px;
  position: absolute;
  left: 0px;
  top: 15px;
  z-index: 1;
}

.stepclass .RFS-StepCircle {
  width: 30px !important;
  height: 30px !important;
  background: transparent !important;
}

.stepclass .RFS-StepCircle:focus {
  outline: none;
}

.stepclass .RFS-StepCircleContent {
  position: relative;
  width: 30px;
  height: 30px;
  background: #49b8ad;
  border: 2px solid #ffffff;
  box-sizing: border-box;
  box-shadow: -1px 4px 4px rgba(30, 133, 130, 0.17);
  z-index: 2;
  font-family: "Mulish", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff !important;
  display: flex;
  align-items: center;
  border-radius: 50%;
  justify-content: center;
  margin: 0 auto;
}

.stepclass .RFS-StepCircleContent.completed {
  width: 20px;
  height: 20px;
  background: #49b8ad;
  box-shadow: -1px 4px 4px rgba(30, 133, 130, 0.17);
  border: 0px;
  font-family: "Mulish", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 13px;
}

.stepclass .RFS-Label {
  display: block;
  margin-top: 6px;
  text-align: center;
  font-family: "Mulish", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 13px;
  color: #1e8582;
}

